import React,{useEffect,useState} from 'react';
import {Bar} from './bar.js';
import {BubbleSort} from '../algoritmo/bubble.js';

export const Visualizer=()=>{

    const [data,setData]=useState([]);
    const [isSorted, setIsSorted]=useState(false);
    const [rangeValue, setRangeValue]=useState(5);

    useEffect(()=>{
        setValues();
    },[rangeValue]);

    const generateRandomNumbers=(max,min)=>{

        //return Math.trunc( Math.random()*(50 - 300) )+300
        return Math.trunc( Math.random()*(max-min ) )+min
    }

    const setValues=()=>{
        let arrayLength=rangeValue;
        let randomNumbers=[];
        for(let i=0; i< arrayLength; i++){
            //Random * (max-min) + min
            randomNumbers.push(generateRandomNumbers(5,400));
            
        }

        setData(randomNumbers);
    }

    const reset=function(){
        setValues();
    }

    const bubble=()=>{
        let n=data;
        const sorted=BubbleSort(n); //obtener el array ya ordenado
        
        setIsSorted(prevState=>!prevState);

        
        let width='';
            if(rangeValue <=15){
                width= '50px';
            }
            else if(rangeValue > 15 && rangeValue <=115){
                width= '7px';
            }
            else if(rangeValue >115){
                width='5px';
            }
        
        for(let i=0; i < sorted.length; i++){
            setTimeout(()=>{
                    document.getElementById(i).style=`
                        background:#3500D3; 
                        width:${width};
                        height:${sorted[i]}px;
                        border:1px solid black;
                    `

                if(i < sorted.length-1){
                    document.getElementById((i+1)).style=`
                        background:#FC4445;
                        width:${width}; 
                        height:${sorted[i]}px; 
                        border:1px solid pink;
                    `
                }
                    
                if(i === sorted.length-1){
                    setIsSorted(prevState=>!prevState);
                }

            },i * 500);
        
        }
        
    }

    if(data.length>0){
            return(
                <div className="app">
                    <div className="options">
                        <button className="btn" disabled={(isSorted) ? true:false} onClick={()=>bubble()}>Bubble Sort</button>
                        <button className="btn  reset" style={{width:'auto'}} disabled={(isSorted) ? true:false} onClick={()=>reset()}>generar nuevo array</button>
                        <input type="range" id="rangeNormal" style={{cursor:'pointer'}} disabled={(isSorted) ? true:false} min="5" max="205" step="10" defaultValue={rangeValue} onMouseUp={e=> setRangeValue(e.target.value)}/>
                        <input type="range" id="rangeResponsive" style={{cursor:'pointer'}} disabled={(isSorted) ? true:false} min="5" max="65" step="10" defaultValue={rangeValue} onChange={e=> setRangeValue(e.target.value)}/>
                        <h2>{rangeValue}</h2>
                    </div>
                    <hr/>
                    {
                          <Bar data={data} rangeValue={rangeValue} color="gray"/>
                    }
                </div>
            );
    }

    return(
            <div className="spinner"></div>
        );
}